import {FBFieldWithPriceBaseOptions, PriceTypeEnum} from "FormBuilder/Fields/Core/FBFieldWithPriceBase.Options";
import {FieldTypeEnum} from "FormBuilder/Fields/Core/FBFieldBase.options";

export class FBTextAreaOptions extends FBFieldWithPriceBaseOptions{
    public DefaultText:string;
    public Placeholder:string;
    public FreeCharOrWords=0;
    public IgnoreSpaces:boolean;


    protected LoadDefaultValues() {
        super.LoadDefaultValues();
        this.IgnoreSpaces=false;
        this.Type=FieldTypeEnum.TextArea;
        this.Label='Text area';

        this.Placeholder='';
        this.DefaultText='';

        this.PriceType=PriceTypeEnum.fixed_amount;
        this.FreeCharOrWords=0;


    }
}